import $ from 'jquery'
import zrender from 'zrender'
import subjectHbs from './subject.hbs'
import { fetch } from '@/util/request'
import { Pie } from '@/components/charts/index'
import tooltipSrc from './images/tooltipBg.png'
import './subject.css'

export default class CaseOccupy {
  constructor() {
    $('#template').append(subjectHbs())
    this.pie = new Pie(document.getElementById('subjectChart'))
  }

  /**
   * 渲染
   * @return {void} void
   */
  render() {
    fetch('fetchSubject', data => {
      let colors = [
        '#631AC4', '#4B4FE2', '#009AE1', 
        '#00C2D1', '#00D3B0', '#26D648', 
        '#89DD44', '#CADD0A', '#DA8F0B', 
        '#C44B0B', '#D912A3'
      ]
      let scales = [0.9, 0.85, 0.75, 0.6, 0.7, 0.9, 0.7, 0.9, 0.4, 0.5, 0.2]
      let dataset = data.zhanbi
      for (let [i, d] of dataset.entries()) {
        d.scale = scales[i]
        d.itemStyle = {
          normal: {
            color: colors[i]
          },
          emphasis: {
            color: zrender.color.lift(colors[i], 0.2)
          }
        }
      }
      
      this.pie.setOption({
        legend: {
          direction: 'row',
          gap: 120,
          textStyle: {
            normal: {
              fontSize: 30
            }
          }
        },
        tooltip: {
          itemStyle: {
            background: `url(${tooltipSrc}) no-repeat center / 100% 100%`,
            fontSize: 30,
            fontColor: '#0AD9FF',
            padding: 16
          }
        },
        label: {
          lineStyle: {
            isOrigin: false,
            length: 20,
            length2: 30,
            width: 2
          },
          labelStyle: {
            fontSize: 30
          },
          pointStyle: {
            radius: 6
          }
        },
        radius: ['0%', '75%'],
        series: dataset
      })
    })
  }
}



