import $ from 'jquery'
import _ from 'lodash'
import { randomString } from '../util'

export default class Tooltip {
  constructor(zr, selector) {
    this.zr = zr

    this.tooltipId = `tooltip${randomString(10)}`
    $(selector).children().eq(0).append(
      `<div id="${this.tooltipId}" style="display: none;position: absolute;"></div>`)
  }

  /**
   * 获取当前实例生成的随机 id
   * @return {String} id
   */
  getId() {
    return this.tooltipId
  }

  /**
   * 显示 tooltip
   * @param  {Object} option 事件对象
   *                         <-
   * legendName {String || Array} 与图例对应的名称
   * titleName  {String}          提示框标题名称
   * value      {Number || Array} 值
   * x          {Number}          鼠标 offsetX 的值
   * y          {Number}          鼠标 offsetY 的值
   *                         ->
   * @param  {Object} style  样式对象
   * @param  {String} fill   图例颜色
   * @return {void}   void
   */
  show(option, style, fill) {
    let { 
      background, borderRadius, padding,
      fontColor, fontSize, formatter } = style       
    let { titleName, legendName, value, x, y } = option    
    
    let isStart = formatter.startsWith('{value}')
    let unit = formatter.replace('{value}', '')    

    // true 为多值类型，false 为单值类型
    // ==============================
    let type = _.isArray(legendName) && _.isArray(value)
    
    // 单值类型模板字符串
    // ================
    let single = `
      <article style="color: ${fontColor};">
        <h1 style="margin-bottom: 12px;font-size: ${fontSize}px">${titleName}</h1>
        <div style="display: flex;">          
          <p style="font-size: ${fontSize - 4}px;">
            ${legendName}:${isStart ? value + unit : unit + value}
          </p>
        </div>
      </article>
    `

    // 多值类型模板字符串
    // ================
    let mulList = ''
    if (type) {
      for (let [i, val] of value.entries()) {
        mulList += `
          <li style="font-size: ${fontSize - 4}px;margin-top: 8px;">
            ${legendName[i]}: ${isStart ? Math.round(val) + unit : unit + Math.round(val)}
          </li>`
      }
    }
    let mul = `
    <article style="color: ${fontColor};">
      <h1 style="font-size: ${fontSize}px">${titleName}</h1>
      <ul style="margin-top: 12px;">        
        ${mulList}
      </ul>
    </article>   
    `

    $(`#${this.tooltipId}`)
      .show()          
      .html(!type ? single : mul)
      .css({
        'left': (function(that) {
          let canvasWidth = that.zr.getWidth()
          let tooltipWidth = $(`#${that.tooltipId}`).width()

          return `${tooltipWidth + x >= canvasWidth - tooltipWidth
            ? x - tooltipWidth - 20
            : x + 20}px`
        }(this)),
        'top': (function (that) {
          let canvasHeight = that.zr.getHeight()
          let tooltipHeight = $(`#${that.tooltipId}`).height()

          return `${tooltipHeight + y >= canvasHeight - tooltipHeight
            ? y - tooltipHeight - 20
            : y + 20}px`
        }(this)),
        'borderRadius': `${borderRadius}px`,
        'background': `${background}`,
        'padding': `${padding}px`
      })
  }

  /**
   * 隐藏提示框
   * @return {void} void
   */
  hide() {
    $(`#${this.tooltipId}`).hide()
  }
}



