import zrender from 'zrender'
import d3 from 'd3'
import _ from 'lodash'
import { Tooltip } from '../util/tooltip/index'
import { Ring } from '../util/shapes/index'
import { Legend } from '../util/legend/index'
import { deepClone } from '../util/util'
import { japancolors10 } from '../util/util'

export default class Pie {
  /**
   * 初始化实例对象
   * @param  {Object} option  配置项
   * @return {void}   void
   */
  initGroup(option) {
    let series = option.series
    let { width, height } = this.canvas

    // 初始化视觉引导线容器
    // ==================
    this.labelContainer = []
    this.labelPointsContainer = []
    for (let [i, d] of series.entries()) {
      this.labelContainer.push(new zrender.Group({ name: d.name }))

      this.labelContainer[i].add(new zrender.Polyline())
      this.labelContainer[i].add(new zrender.Text({
        legendName: d.name,
        position: [width / 2, height / 2],
        style: {
          opacity: 0
        }
      }))
      this.labelPointsContainer.push(new zrender.Circle({
        name: d.name,
        shape: {
          cx: width / 2,
          cy: height / 2
        }
      }))

      this.zr.add(this.labelContainer[i])
      this.zr.add(this.labelPointsContainer[i])
    }

    // 初始化饼图的容器
    // ==============
    this.pieGroup = new zrender.Group()

    // 计算内外半径
    let innerRate = Number(this.radius[0].replace('%', '')) / 100 / 2
    let outerRate = Number(this.radius[1].replace('%', '')) / 100 / 2
    let innerRadius = width > height
      ? height * innerRate
      : width * innerRate
    let outerRadius = width > height
      ? height * outerRate
      : width * outerRate
    this.outerRadius = outerRadius

    for (let d of series) {
      let scale = d.scale || 1
      this.pieGroup.add(new Ring({
        originOuterRadius: outerRadius * scale,
        name: d.name,
        titleName: this.tooltip.titleName,
        legendName: d.name,
        shape: {
          cx: width / 2,
          cy: height / 2,
          innerRadius,
          outerRadius: outerRadius * scale
        },
        style: {
          fill: 'none'
        }
      }))
    }
    this.zr.add(this.pieGroup)

    // 初始化图例容器
    // ============
    if (this.legendConfig.show) {
      this.legend = new Legend(this.zr, this.legendConfig, this.padding)
      this.legendContainer = this.legend.getGroup()
    }
  }

  /**
   * 初始化基本配置
   * 该方法只会执行一次，只能将永远不会改变的属性放入其中
   * @param  {Object} option 配置选项
   * @return {void}   void
   */
  initConfig(option) {
    // 坐标系统的基本信息
    // ================
    this.canvas = {
      width: this.zr.getWidth(),
      height: this.zr.getHeight()
    }

    this.padding = {
      left: this.canvas.width * 0.05,
      top: this.canvas.height * 0.15
    }

    // 初始化颜色函数
    // =============
    this.colors = japancolors10

    // 是否正在进行动画
    // ==============
    this.isAnimate = false

    // 是否初始化
    // =========
    this.isInit = false

    this.tooltip = _.merge({
      show: true,
      titleName: '数据来源',
      itemStyle: {
        background: 'rgba(11, 16, 19, .66)',
        borderRadius: 4,
        padding: 8,
        fontColor: '#FCFAF2',
        fontSize: 18,
        formatter: '{value}'
      }
    }, option.tooltip)
    
    this.label = _.merge({
      show: true,
      lineStyle: {
        isOrigin: true,
        type: 'solid',
        width: 1,
        length: 10,
        length2: 40,
        smooth: 0.1
      },
      labelStyle: {
        fontSize: 12,
        fontFamily: 'sans-serif',
        fontStyle: 'normal',
        fontWeight: 'normal',
        margin: 10
      },
      pointStyle: {
        isShow: true,
        position: 'start',
        radius: 4
      }
    }, option.label)

    this.legendConfig = _.merge({
      show: false,
      direction: 'column', 
      gap: 40,
      offsetX: 0,
      offsetY: 0,
      labelStyle: {
        normal: {
          color: '#FCFAF2',
          fontSize: 12,
          fontStyle: 'normal',
          fontFamily: 'sans-serif',
          fontWeight: 'normal'
        },
        blur: {
          color: '#828282'
        }
      },
      itemStyle: {
        symbol: 'circle',
        size: 4,
        margin: 10
      }
    }, option.legend)
    
    this.radius = _.merge(['0%', '75%'], option.radius)
  }

  constructor(selector) {
    this.zr = zrender.init(selector)

    // 初始化 tooltip 组件
    // ==================
    this.tip = new Tooltip(this.zr, selector)    
  }

  /**
   * 创建饼图数据
   * @param  {Array} series 配置参数
   * @return {Array} 返回饼图弧度数组
   */
  createPieData(series) {
    return d3.layout.pie()
      .value(d => d.value)
      .sort(null)(series)
  }

  /**
   * 绘制单个圆环
   * @param  {Number} index   下标
   * @param  {Object} pieData 饼图数据
   * @param  {Object} option  配置选项
   * @return {void}   void
   */
  drawRing(index, pieData, option) {
    let itemStyle = _.merge({
      normal: {
        color: this.colors(index),
        borderColor: 'transparent',
        borderWidth: 0,
        opacity: 1,
        shadowColor: 'rgba(0, 0, 0, 0)',
        shadowOffsetX: 0,
        shadowOffsetY: 0,
        shadowBlur: 10
      },
      emphasis: {
        color: zrender.color.lift(this.colors(index), 0.2),
        borderColor: 'transparent',
        borderWidth: 0,
        opacity: 1,
        shadowColor: 'rgba(0, 0, 0, .54)',
        shadowOffsetX: 0,
        shadowOffsetY: 0,
        shadowBlur: 10
      }
    }, option.itemStyle)
    
    let normal = {
      fill: itemStyle.normal.color,
      stroke: itemStyle.normal.borderColor,
      lineWidth: itemStyle.normal.borderWidth,
      opacity: itemStyle.normal.opacity,
      shadowColor: itemStyle.normal.shadowColor,
      shadowOffsetX: itemStyle.normal.shadowOffsetX,
      shadowOffsetY: itemStyle.normal.shadowOffsetY,
      shadowBlur: itemStyle.normal.shadowBlur
    }
    let emphasis = {
      fill: itemStyle.emphasis.color,
      stroke: itemStyle.emphasis.borderColor,
      lineWidth: itemStyle.emphasis.borderWidth,
      opacity: itemStyle.emphasis.opacity,
      shadowColor: itemStyle.emphasis.shadowColor,
      shadowOffsetX: itemStyle.emphasis.shadowOffsetX,
      shadowOffsetY: itemStyle.emphasis.shadowOffsetY,
      shadowBlur: itemStyle.emphasis.shadowBlur
    }
    let { startAngle, endAngle, value } = pieData
    let percent = Math.round((endAngle - startAngle) / (Math.PI * 2) * 100)
    let arc = this.pieGroup.childAt(index)

    // 动画渲染
    // ========    
    this.isAnimate = true
    arc
      .attr({
        value: `${value} (${percent}%)`,
        style: normal
      })
      .animateTo({
        shape: {
          startAngle, endAngle
        }
      }, 1000, 0, 'elasticOut', () => {
        this.isAnimate = false
      })

    // 绘制视觉引导线
    // ============
    if (this.label.show) {
      this.drawLabel(index, arc.shape.outerRadius, startAngle, endAngle, arc.style.fill)
    }

    // 绑定鼠标悬浮事件
    // ==============    
    arc
      .off('mousemove')
      .off('mouseover')
      .off('mouseout')
      .on('mousemove', e => {
        // 显示 tooltip
        if (this.tooltip.show && !this.isAnimate) { 
          let { legendName, titleName, value: val } = e.target
          let { offsetX, offsetY } = e       
          this.tip.show({
            legendName, titleName, value: val,
            x: offsetX, y: offsetY
          }, this.tooltip.itemStyle, arc.style.fill)
        }
      })
      // 鼠标进出改变样式
      .on('mouseover', () => {
        if (!this.isAnimate) {
          arc.animateTo({
            shape: {
              outerRadius: arc.originOuterRadius * 1.05
            },
            style: emphasis
          }, 500, 0, 'elasticOut')
        }
      })
      .on('mouseout', () => {
        if (!this.isAnimate) {
          // 改变样式
          arc.animateTo({
            shape: {
              outerRadius: arc.originOuterRadius
            },
            style: normal
          }, 500, 0, 'elasticOut')
        }
        // 隐藏提示框
        this.tip.hide()
      })
  }

  /**
   * 绘制视觉引导线
   * @param  {Number} index       下标
   * @param  {Number} outerRadius 圆弧的实际半径
   * @param  {Number} startAngle  其实弧度
   * @param  {Number} endAngle    终止弧度
   * @param  {String} fill      颜色
   * @return {void}   void
   */
  drawLabel(index, outerRadius, startAngle, endAngle, fill) {
    let group = this.labelContainer[index]
    
    let polyline = group.childAt(0)
    let text = group.childAt(1)
    let point = this.labelPointsContainer[index]

    // 1.绘制引导线
    // ===========
    let { type, length, length2, smooth, width: lineWidth } = this.label.lineStyle

    // 扇形的中心店弧度值
    // ===============
    let centerAngle = endAngle - (endAngle - startAngle) / 2

    // 饼图的中心点
    // ==========
    let cx = this.canvas.width / 2
    let cy = this.canvas.height / 2

    // 求出起始点
    // =========
    let x1, y1
    if (this.label.lineStyle.isOrigin) {
      x1 = Math.cos(centerAngle) * outerRadius + cx
      y1 = Math.sin(centerAngle) * outerRadius + cy
    } else {
      x1 = Math.cos(centerAngle) * this.outerRadius + cx
      y1 = Math.sin(centerAngle) * this.outerRadius + cy
    }

    // 求出第二个落点
    // ============
    let x2 = Math.cos(centerAngle) * (this.outerRadius + length) + cx
    let y2 = Math.sin(centerAngle) * (this.outerRadius + length) + cy

    // 求出第三个落点
    // =============
    let x3 = x2 > cx ? x2 + length2 : x2 - length2
    let y3 = y2

    polyline
      .attr({
        style: {
          stroke: fill,
          lineWidth,
          lineDash: (function() {
            switch(type) {            
            case 'solid':
              return null
            case 'dashed':
              return [4, 5]   
            case 'dotted':
              return [1, 5]         
            default:
              console.log('work')
            }
          }())
        }
      })
      .animateTo({
        shape: {
          points: [
            [x1, y1], [x2, y2], [x3, y3]
          ],
          smooth
        }
      }, 500, 0, 'linear')

    // 2.绘制文字
    // =========    
    let { fontSize, fontFamily, fontStyle, fontWeight, margin } = this.label.labelStyle
    text
      .attr({
        style: {
          text: text.legendName,
          textFill: fill,
          fontSize,
          fontFamily,
          fontStyle,
          fontWeight
        }
      })
      .animateTo({
        position: [
          x3 > cx ? x3 + margin : x3 - text.getBoundingRect().width - margin, 
          y3 - text.getBoundingRect().height / 2
        ],
        style: {
          opacity: 1
        }   
      }, 500, 0, 'linear')

    // 3.绘制顶点
    // =========
    let { radius, position } = this.label.pointStyle
    let centerX, centerY
    if (position === 'start') {
      centerX = x1
      centerY = y1
    } else if (position === 'end') {
      centerX = x3
      centerY = y3
    }
    if (!this.label.pointStyle.isShow) {
      point.hide()
    }
    point
      .attr({
        shape: {
          r: radius
        },
        style: {
          fill: new zrender.RadialGradient(0.5, 0.5, 0.5, [
            {
              offset: 0,
              color: fill
            },
            {
              offset: 0.4,
              color: fill
            },
            {
              offset: 0.5,
              color: 'transparent'
            }
          ]),
          stroke: fill,
          lineWidth: 1
        }
      })
      .animateTo({
        shape: {
          cx: centerX,
          cy: centerY
        }
      }, 500, 0, 'linear')
  }

  /**
   * 绑定图例的事件
   * @param  {String} name   图例名称
   * @return {void}   void
   */
  bindLegendEvent(name) {
    // 通过 name 属性，查找响应的 Group 实例
    // ==================================
    let legendGroup = this.legendContainer.filter(d => d.name === name)[0]

    let pieGroup = this.pieGroup.childOfName(name)
    let labelGroup = this.labelContainer.filter(d => d.name === name)[0]
    let labelPointsGroup = this.labelPointsContainer.filter(d => d.name === name)[0]
    
    // 图例对应饼图的颜色
    // ===============
    let itemFill = pieGroup.style.fill

    // 更新数据时，还原所有实例的属性为初始状态
    // =================================
    this.legend.init(name, itemFill)
    
    pieGroup.show()
    labelGroup.show()
    labelPointsGroup.show()

    // 深拷贝数据到缓冲区
    // ================
    this.buffer = deepClone(this.series)

    // 定义点击事件
    // ==========
    legendGroup
      .off('click')
      .on('click', () => {
        switch(legendGroup.status) {
        case 'normal': 
          // 改变图例的样式
          this.legend.toggle(name, itemFill)
        
          // 1.改变缓冲区数据
          this.buffer.filter(d => d.name === name)[0].value = 0        

          // 2.隐藏对应的扇形和引导线
          pieGroup.hide()
          labelGroup.hide()
          labelPointsGroup.hide()

          // 3.根据缓冲区数据生成新的 piedata
          this.pieData = this.createPieData(this.buffer)

          // 4.使用新的 piedata 绘制新的饼图
          for (let [i, d] of this.pieData.entries()) {
            this.drawRing(i, d, this.series[i])
          }
          break
        case 'blur':
          // 改变图例的样式
          // =============
          this.legend.toggle(name, itemFill)

          // 1.改变缓冲区数据
          this.buffer.filter(d => d.name === name)[0].value 
            = this.series.filter(d => d.name === name)[0].value

          // 2.隐藏对应的扇形和引导线，并将 isShow 设置为 true
          pieGroup.show()
          labelGroup.show()
          labelPointsGroup.show()

          // 3.根据缓冲区数据生成新的 piedata
          this.pieData = this.createPieData(this.buffer)

          // 4.使用新的 piedata 绘制新的饼图
          for (let [i, d] of this.pieData.entries()) {
            this.drawRing(i, d, this.series[i])
          }
          break
        default: 
          console.log('work')
        }
      })
  }

  /**
   * 渲染
   * @param  {Object} option 配置参数
   * @return {void}   void
   */
  setOption(option) {
    // 初始化容器
    // =========
    if (!this.isInit) {
      this.initConfig(option)
      this.initGroup(option)            
    }
    
    // 制作 pieData
    // ============
    this.series = option.series
    this.pieData = this.createPieData(this.series)

    // 绘制饼图
    // =======
    for (let [i, d] of this.pieData.entries()) {
      this.drawRing(i, d, this.series[i])
    }    
    
    // 定义图例点击事件
    // ==============
    if (this.legendConfig.show) {
      for (let group of this.legendContainer) {
        this.bindLegendEvent(group.name)
      }
    }

    // 初始化完成，切换开关
    // =================
    this.isInit = true
  }
}



