import $ from 'jquery'
 
import { fetch } from '@/util/request'
import hbs from './hbs/index.hbs'
import listHbs from './hbs/list.hbs'
import './index.css'

export default class CaseType {
  constructor() {
    $('#template').append(hbs())
  }

  /**
   * 渲染
   * @return {void} void
   */
  render() {
    fetch('fetchCaseType', data => {
      console.log(data.caseType)
      $('.case-list').html(listHbs(data.caseType))
    })
  }
}



