import zrender from 'zrender'

export default class Legend {
  /**
   * 初始化容器
   * @param  {Object} option 配置项
   * @return {Array}  返回容器本身
   */
  initGroup(option) {
    const data = option.data
    const symbol = option.itemStyle.symbol    

    let legendContainer = []
    for (let [i, name] of data.entries()) {
      legendContainer.push(new zrender.Group({
        status: 'normal',
        name
      }))

      // 根据 symbol 类型添加图标实例
      switch(symbol) {
      case 'ring':
        legendContainer[i].add(new zrender.Circle({ name: 'innerItem' }))
        legendContainer[i].add(new zrender.Circle({ name: 'outerItem' }))
        break
      case 'circle':
        legendContainer[i].add(new zrender.Circle({ name: 'item' }))
        break
      case 'rect':
        legendContainer[i].add(new zrender.Rect({ name: 'item' }))
        break
      default: 
        console.log('work')
      }
      legendContainer[i].add(new zrender.Text({ name: 'label' }))

      this.zr.add(legendContainer[i])
    }
    return legendContainer
  }

  /**
   * 绘制图例
   * @param  {Object} option  配置项
   * @param  {Object} padding 环境边距
   * @return {void}   void
   */
  drawLegends(option, padding) {
    let { left, top } = padding
    let { itemStyle, labelStyle } = this.config      

    for (let [index, group] of this.legendContainer.entries()) {      
      let item, item2
      let label = group.childOfName('label')
    
      // 绘制图标
      switch(itemStyle.symbol) {
      case 'ring':
        item = group.childOfName('innerItem')
        item2 = group.childOfName('outerItem')
        item.attr({
          shape: { r: itemStyle.size / 2 }
        })
        item2.attr({
          shape: { r: itemStyle.size }
        })
        break
      case 'circle':
        item = group.childOfName('item')
        item.attr({ 
          shape: { r: itemStyle.size }  
        })
        break
      case 'rect':
        item = group.childOfName('item')        
        item.attr({
          shape: { 
            width: itemStyle.size,
            height: itemStyle.size,
            y: -itemStyle.size / 2
          }
        })
        break
      default:
        console.log('work')
      }      

      // 绘制文字标签
      label
        .attr({
          style: {
            text: group.name,
            textFill: labelStyle.normal.color,
            fontSize: labelStyle.normal.fontSize,
            fontFamily: labelStyle.normal.fontFamily,
            fontStyle: labelStyle.normal.fontStyle,
            fontWeight: labelStyle.normal.fontWeight,
            textVerticalAlign: 'middle'
          },
          position: [itemStyle.size + itemStyle.margin, 1]
        })
        
      // 设置容器的位移
      let { direction, gap, offsetX, offsetY } = this.config
      let itemBounding = item.getBoundingRect()
      let labelBounding = label.getBoundingRect()
      let legendWidth = itemBounding.width + labelBounding.width + itemStyle.margin
      let legendHeight = itemBounding.height > labelBounding.height 
        ? itemBounding.height : labelBounding.height

      this.legendWidth.push(legendWidth)
      
      let x, y      

      // 水平分布
      if (direction === 'row') {
        let lastWidth = 0
        for (let j = 1; j <= index; j++ ) {          
          lastWidth += this.legendWidth[j - 1]          
        }
        
        x = left + lastWidth + index * gap
        y = top / 2
      }
      // 垂直分布
      if (direction === 'column') {
        x = left
        y = top + (legendHeight + gap) * index
      }

      group.attr({
        position: [x + offsetX, y + offsetY]
      })
    }
  }

  /**
   * 实例化
   * 该方法只负责绘制图例，以及图例的 toggle 样式显示事件
   * 而图例的功能由父元素负责
   * @param  {Object} zr      zr 环境对象
   * @param  {Object} config  配置项
   * @param  {Object} padding 环境边距
   * @return {Array}  返回容器数组
   */
  constructor(zr, config, padding) {
    this.zr = zr
    this.config = config
    this.legendWidth = []
    
    if (this.config.show) {
      this.legendContainer = this.initGroup(config)      
      this.drawLegends(config, padding)
    }
  }

  /**
   * 获取生成的实例对象集合
   * @return {Array} 返回该集合
   */
  getGroup() {
    return this.legendContainer
  }

  /**
   * 初始化图例状态
   * @param  {String} name     图例名称作为查找条件
   * @param  {String} itemFill 环境颜色，作为 item 的颜色
   * @return {void}   void
   */
  init(name, itemFill) {
    let { labelStyle, itemStyle } = this.config
    let { symbol } = itemStyle    
    
    let group = this.legendContainer.filter(d => d.name === name)[0]
    let item, item2
    let label = group.childOfName('label')

    if (symbol === 'ring') {
      item = group.childOfName('innerItem')
      item2 = group.childOfName('outerItem')
      item.attr({
        style: {
          fill: itemFill
        }
      })
      item2.attr({
        style: {
          fill: 'none',
          stroke: itemFill,
          lineWidth: 1
        }
      })
    } else {
      item = group.childOfName('item')
      item.attr({
        style: {
          fill: itemFill
        }
      })
    }
    
    label.attr({
      style: {
        textFill: labelStyle.normal.color
      }
    })
    group.status = 'normal'
  }

  /**
   * 点击事件改变图例的状态
   * @param  {String} name     图例名称作为查找条件
   * @param  {String} itemFill 环境颜色，作为 item 的颜色
   * @return {void}   void
   */
  toggle(name, itemFill) {
    let { labelStyle, itemStyle } = this.config
    let { symbol } = itemStyle

    let group = this.legendContainer.filter(d => d.name === name)[0]
    let item, item2
    let label = group.childOfName('label')    

    let status = group.status   
     
    switch(status) {
    case 'normal':
      if (symbol === 'ring') {        
        item = group.childOfName('innerItem')
        item2 = group.childOfName('outerItem')
        item.attr({
          style: {
            fill: labelStyle.blur.color
          }
        })
        item2.attr({
          style: {
            fill: 'none',
            stroke: labelStyle.blur.color,
            lineWidth: 1
          }
        })
      } else {
        item = group.childOfName('item')
        item.attr({
          style: {
            fill: labelStyle.blur.color
          }
        })
      }

      label.attr({
        style: {
          textFill: labelStyle.blur.color
        }
      })
      
      group.status = 'blur'
      break
    case 'blur':
      if (symbol === 'ring') {
        item = group.childOfName('innerItem')
        item2 = group.childOfName('outerItem')
        item.attr({
          style: {
            fill: itemFill
          }
        })
        item2.attr({
          style: {
            fill: 'none',
            stroke: itemFill,
            lineWidth: 1
          }
        })
      } else {
        item = group.childOfName('item')
        item.attr({
          style: {
            fill: itemFill
          }
        })
      }

      label.attr({
        style: {
          textFill: labelStyle.normal.color
        }
      })
      
      group.status = 'normal'
      break
    default:
      console.log('work')
    }
  }
}



